// -----------------------------------------------------------------------------
// This file contains all footer styles
// -----------------------------------------------------------------------------
footer {
	background: var(--color-1-normal);
	height: 90px;
	color: lightgray;
	display: flex;
	align-items: center;
	padding: 0 3vw;
	width: 100%;
	bottom: 0;
	margin-top: 2rem;
}

.footermenu {
	margin-left: auto;
	display: flex;
}

.footermenu h4 {
	margin: 0 0.3vw;
	border-right: 1px solid var(--footer-text-color);
	padding-right: 0.4vw;
	cursor: pointer;
}

@media only screen and (max-width: 700px) {
	footer {
		font-size: 0.6rem;
	}
}
