@media only screen and (max-width: 700px) {
  article {
    grid-template-columns: 1fr;
    section {
      grid-column: 1;
    }
    blockquote {
      grid-column: 1;
    }
  }

  .diagram {
    grid-row: 1;
  }

  .informationBlock {
    grid-template-columns: 1fr 3fr;
  }
}
